/**
 * 配置文件 - 环境切换
 * 
 * 使用说明：
 * 1. 开发环境：使用 mock 数据，设置 USE_MOCK = true
 * 2. 生产环境：使用真实 API，设置 USE_MOCK = false
 */

// 是否使用 mock 数据（true: 使用mock数据, false: 使用真实API）
export const USE_MOCK = true

// API 基础地址（生产环境）
export const API_BASE_URL = 'https://api.example.com'

// 当前环境
export const ENV = USE_MOCK ? 'development' : 'production'

// 日志输出（开发环境打印日志）
export const DEBUG = USE_MOCK

